import React, { useState, Fragment } from "react";
import { useForm } from 'react-hook-form';
//import { Form, Button, Modal } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import styled from 'styled-components';
import { Form as Forma, Input, Select, Col, Row, Radio, Button as Buttona, Checkbox, Alert } from "antd";
import axios from 'axios';


const layout = {
    labelCol: {
        span: 6,
    },
    wrapperCol: {
        span: 14,
    },
};
const tailLayout = {
    wrapperCol: {
        offset: 8,
        span: 8,
    },
};


function AddDB(props) {

    const [isLoading, setIsLoading] = useState(false);
    //const [sendFail, setSendFail] = useState(false);
    const [errorShow, setErrorShow] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");




    //const [dbName, setDbName] = useState("");

    const { register, handleSubmit, errors } = useForm(); // initialize the hook
    const onSubmit = (data) => {
        setIsLoading(true);

        console.log(data);

        axios.post('http://localhost:8080/api/test/dbs', data, {
            headers: {
                'Content-Type': 'application/json'
            }
        }).then(res => {
            setIsLoading(false);
            //setSendMessage(res.data.message);
            //setModalShow(true);
            props.history.push("/home");
            //window.location.reload();

        })
            .catch(err => {
                setIsLoading(false);
                setErrorMessage(err.response.data);
                //setModalShow(true);
                console.log(err.response.data);
            });

    };



    const DIV = styled.div`
  
  border-radius: 25px;
  
  border: 2px solid #C6D6E7;
  background-color: #f7f7f7; 
  padding-top: 50px;
  padding-right: 80px;
  padding-bottom: 80px;
  padding-left: 80px;
  text-size-adjust: auto;
`;

    const onFinish = (data) => {
        console.log('Success:', data);
        setErrorMessage("");
        axios.post('http://localhost:8080/api/test/dbs', data, {
            headers: {
                'Content-Type': 'application/json'
            }
        }).then(res => {
            //setIsLoading(false);
            //setSendMessage(res.data.message);
            //setModalShow(true);
            props.history.push("/home");
            //window.location.reload();

        })
            .catch(err => {
               // setIsLoading(false);
               setErrorMessage(err.response.data);
                //setModalShow(true);
                console.log(err.response.data);
            });
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    return (


        <>

            <Row>
            <Col span={12} offset={6}>
            {errorMessage&&<Alert message={errorMessage} type="error" />}
            </Col>
            </Row>
            <Row align="middle" >
            
                <Col span={12} offset={6} style={{ border: '1px solid #f0f0f0', borderRadius: '5px', marginBottom: "10px", marginTop: "20px" , padding: "20px"  }}>

                    {/* <Forma
                        style={{ margin: "20px" }}
                        layout="horizontal"
                        labelCol={{
                            span: 5,
                        }}
                        wrapperCol={{
                            span: 14,
                        }}
                    >



                    </Forma> */}


                    <Forma
                        {...layout}
                        name="basic"
                        initialValues={{
                            remember: true,
                        }}
                        onFinish={onFinish}
                        // onFinishFailed={onFinishFailed}
                    >

                        <Forma.Item label="IP wersja" name="ipVer"
                        rules={[
                            {
                                required: true,
                                message: 'Wubierz wersje IP!',
                            }]}
                        ref={register}>
                            <Radio.Group name="ipVer" as="select" >
                                <Radio.Button value="ipv4">IPv4</Radio.Button>
                                <Radio.Button value="ipv6">IPv6</Radio.Button>
                            </Radio.Group>
                        </Forma.Item>

                        <Forma.Item
                            label="Nazwa Bazy"
                            name="nameDb"
                            rules={[
                                {
                                    required: true,
                                    message: 'Podaj nazw bazy!',
                                }
                                // { min: 5, message: 'Username must be minimum 5 characters.' },
                                // {
                                //     pattern:/^[0-9]*$/,
                                //     message: `Password Pattern`
                                //  }
                            ]}
                        >
                            <Input />
                        </Forma.Item>

                        <Forma.Item
                            label="Opis bazy"
                            name="descDb"
                            rules={[
                                {
                                    required: true,
                                    message: 'Podaj opis bazy!',
                                },
                            ]}
                        >
                            <Input />
                        </Forma.Item>

        

                        <Forma.Item {...tailLayout}>
                            <Buttona type="primary" htmlType="submit">
                                Zapisz
                            </Buttona>
                        </Forma.Item>
                    </Forma>
                </Col>
            </Row>


            {/* <DIV className="container mt-5">

                {sendMessage && <Form.Label style={{ maxWidth: "100%" }} className="alert alert-danger" column sm="2">
                    {sendMessage}
                </Form.Label>}

                {/* <div style={{marginTop: "20px", position: "fixed"}}>
                {sendMessage && (
                    <div className="form-group">
                        <div className="alert alert-danger" role="alert">
                            {sendMessage}
                        </div>
                    </div>
                )}
            </div>
            <div> */}
            {/* <Form onSubmit={handleSubmit(onSubmit)}> */}
            {/* <Form.Label inline column sm="2">
          First Name
    </Form.Label>
        <Form.Control inline name="firstname" ref={register} />  */}

            {/* <Form.Label inline column sm="2">
                        IP Ver.
    </Form.Label>
                    <Form.Control name="ipVer" as="select" ref={register}>
                        <option>ipv4</option>
                        <option>ipv6</option>
                    </Form.Control>
                    <Form.Label column sm="2">
                        Nazwa Bazy
    </Form.Label>
                    <Form.Control name="nameDb" isValid={!errors.lastname} isInvalid={errors.lastname} ref={register({ required: true })} />
                    <Form.Control.Feedback type="invalid">{errors.lastname && 'Last name is required.'}</Form.Control.Feedback>
                    {/* {errors.lastname && 'Last name is required.'} */}
            {/* <Form.Label column sm="2">
                        Opis bazy
    </Form.Label>
                    <Form.Control name="descritponDb" isValid={!errors.age} isInvalid={errors.age} ref={register({
                        required: true,
                        // pattern: /\d+/
                    })} />

                    <Form.Control.Feedback type="invalid">{errors.age && 'Last name is required.'}</Form.Control.Feedback>
                    {errors.age && 'Please enter number for age.'}
                    <Button style={{ marginTop: "20px", float: "right" }} type="submit"> Send </Button>
                </Form>




            </DIV> */}
        </>






    );
}

export default AddDB;

